<template>
	<ul>
		<li v-for="(plan, key) in data" :key="key" class="m-plan">
			<i class="u-icon">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-if="plan.public" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
			</i>
			<a class="u-title" target="_blank" :href="`/item/#/plan_view/${plan.id}`">{{ plan.title || "无标题" }}</a>
			<div class="u-desc">
				<time class="u-desc-subitem">
					<i class="el-icon-finished"></i>
					发布 :
					{{ dateFormat(plan.created) }}
				</time>
				<time class="u-desc-subitem">
					<i class="el-icon-refresh"></i>
					更新 :
					{{ dateFormat(plan.updated) }}
				</time>
			</div>
			<el-button-group class="u-action">
				<el-button size="mini" icon="el-icon-edit" title="编辑" @click="plan_edit(plan.id)"></el-button>
				<el-button size="mini" icon="el-icon-delete" title="删除" @click="plan_delete(plan.id)"></el-button>
			</el-button-group>
		</li>
	</ul>
</template>

<script>
import { delMyPlans } from "../service/item_plan";
import dateFormat from "../utils/dateFormat";
export default {
	name: "item_plan",
	props: ["data"],
	methods: {
		plan_edit: function (id) {
			location.href = "./#/item_plan/" + id;
		},
		plan_delete: function (id) {
			this.$confirm("确认是否删除该物品清单？", "提示", {
				confirmButtonText: "确定",
				cancelButtonText: "取消",
				type: "warning",
			}).then(() => {
				delMyPlans(id).then((res) => {
					this.$emit("refresh");
					this.$message({
						message: "删除成功",
						type: "success",
					});
				});
			});
		},
		dateFormat(val) {
			return dateFormat(new Date(val * 1000));
		},
	},
};
</script>

